<template>
  <div>
    <mu-appbar style="width: 100%;" color="#4ed995" class="header">
      <a @click="()=>{$router.go(-1)}">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span>路线录入</span>
    </mu-appbar>
    <div>
      <div class="naviTitle">
        <span>线路信息</span>
      </div>
      <div style class="contentAdd">
        <div class="flexdis">
          <div style="width:100px;">监测时间</div>
          <div class="dateTime" @click="selectTime()">
            <input type="text" id="input" placeholder="请选择录入时间" disabled v-model="value6" />
          </div>
        </div>
        <div class="flexdis">
          <div style="width:100px;">监测类型</div>
          <div class="dateTime" @click="selectDataPicoptions" id="classPicoptions">
            <!-- <select name v-model="normal.value1" class="select">
              <option :value="item" v-for="(item,index) in options" :key="index">{{item}}</option>
            </select>-->
            <input type="text" disabled v-model="normal.value1" class="input" placeholder="请选择监测类型" />
          </div>
        </div>
        <div class="flexdis">
          <div style="width:100px;">车辆编号</div>
          <div class="dateTime" @click="selectDataPicCar" id="classPicCar">
            <!-- <input type="text" id="input" placeholder="请输入车俩编号" v-model="carNum" /> -->
            <!-- <select name v-model="carNum" class="select">
              <option :value="item" v-for="(item,index) in optionscarNum" :key="index">{{item}}</option>
            </select>-->
            <input type="text" placeholder="请选择车辆编号" v-model="carNum" class="input" disabled />
          </div>
        </div>
        <div class="flexdis">
          <div style="width:100px;">监测人员</div>
          <div class="dateTime" @click="selectDataPicPerson" id="classPicPerson">
            <input type="text" name="" class="input" placeholder="请选择监测人员" v-model="person" disabled>
          </div>
        </div>
        <div class="flexdis">
          <div style="width:100px;">任务编号</div>
          <div class="dateTime">
            <input type="text" id="input" disabled placeholder="请输入任务编号" v-model="taskId" />
          </div>
        </div>
        <div class="flexdis">
          <div style="width:100px;">点位数量</div>
          <div class="dateTime">
            <input type="text" id="input" placeholder="请输入点位数" v-model="pointNum" />
          </div>
        </div>
      </div>
      <div class="subBack">
        <div class="divWidth" @click="confirmSubmit()">
          <span>提交</span>
        </div>
      </div>
    </div>
    <mu-container>
      <mu-bottom-sheet :open.sync="openTime">
        <mu-list @item-click="closeBottomSheetTime">
          <mu-sub-header>筛选时间</mu-sub-header>
          <van-datetime-picker
            v-model="currentDate"
            type="date"
            :min-date="minDate"
            @confirm="confirmTime"
            @cancel="close"
          />
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
import { Dialog } from 'vant';
import "vant/lib/dialog/style";
export default {
  name: "addDataList",
  data() {
    return {
      value6: null,
      options: ["定期", "不定期"],
      token: "",
      carNum: "",
      person: "",
      taskId: "",
      pointNum: "",
      normal: {
        value1: ""
      },
      loading: false,
      openTime: false,
      currentDate: new Date(),
      minDate: new Date("1970-07-01"),
      optionscarNum: [],
      optionsperson: ["代璐", "周瑞斌", "谢子琪", "宁东成", "魏晓宁"]
    };
  },
  methods: {
    selectDataPicoptions() {
      // this.opened = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicoptions, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.options, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择类型", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.normal.value1 = selectArr[0];
          // _this.opened = true;
        }
      });
    },
    // 车辆编号
    selectDataPicCar() {
      // this.opened = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicCar, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.vehicleList, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择类型", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.carNum = selectArr[0];
          // _this.opened = true;
        }
      });
    },
    // 提示框
     confirmSubmit() {
      var _this = this;
      Dialog.confirm({
        title: "请确认是否提交数据？",
        message: "监测人员为："+this.person
      })
        .then(() => {
          // on confirm
         _this.addRoad();
        })
        .catch(() => {
          // on cancel
          Toast("您点击了取消");
        });
    },
    // 检测人员
    selectDataPicPerson() {
      // this.opened = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicPerson, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.optionsperson, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择类型", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.person = selectArr[0];
          // _this.opened = true;
        }
      });
    },

    closeBottomSheetTime() {
      this.openTime = false;
    },
    confirmTime(value) {
      this.openTime = false;
      this.value6 = AppBace.filter(value);
    },
    close() {
      this.openTime = false;
    },
    selectTime() {
      this.openTime = true;
    },
    fullscreen() {
      this.loading = this.$loading();
    },
    async addRoad() {
      this.fullscreen();
      var url = "/mobile/routing";
      var name = AppBace.filter(this.value6);
      var person = this.person;
      var pointNumber = this.pointNum;
      var taskId = this.taskId;
      var type = this.normal.value1;
      var vehicleId = this.carNum;
      var res = await AppBace.request(
        url,
        "POST",
        {
          name,
          person,
          pointNumber,
          taskId,
          type,
          vehicleId
        },
        this.token
      );
      console.log(res);
      var message = res.data.message;

      if (message === "success") {
        this.loading.close();
        Toast(message);
        this.$router.replace("/dataListControl");
      } else {
        this.loading.close();
        Toast(res.data.info);
      }
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    this.optionscarNum = this.$store.state.vehicleList;
  }
};
</script>
<style scoped>
@import "../../../static/css/model.css";
.subBack {
  width: 100%;
  padding: 0 20px;
  text-align: center;
}
.divWidth {
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #4ed995;
  color: white;
  font-size: 16px;
  border-radius: 10px;
  margin-top: 10px;
  font-weight: 600;
}
.flexdis {
  padding: 0 10px;
  display: flex;
  flex-direction: row;
  height: 50px;
  align-items: center;
  background: white;
  border-radius: 10px;
}
.dateTime {
  width: 100%;
}
/* 标题栏 */
.naviTitle {
  margin-top: 10px;
  font-size: 14px;
  font-weight: 600;
  /* margin-left: 5px; */
  margin-left: 5px;
  border-left: #4ed995 5px solid;
  margin-bottom: 10px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  padding-left: 5px;
}
.contentAdd {
  width: 100%;
  /* border-radius: 10px; */
  padding: 0 5px;
}
#input {
  border-radius: 10px;
  width: 100%;
  outline: none;
  padding: 0 10px;
  height: 30px;
  border: 1px solid #ccc;
  background: white;
}
.select {
  width: 100%;
  height: 30px;
  border-radius: 10px;
  outline: none;
  border: 1px solid #ccc;
  background: white;
}
</style>



